nav {
  position: fixed;
  width: 100%;
  z-index: 100;
  //box-shadow: var(--box-shadow-5);

  .socials__nav {
    background-color: var(--dark-grey-color);

    .socials__nav-content {
      display: flex;
      justify-content: space-between;
      padding: 0.6rem 0;
      @media screen and (max-width: 600px) {
        padding: 0.1rem;
      }
      div {
        display: flex;
        align-items: center;
        gap: 20px;

        p {
          display: flex;
          align-items: center;
          color: white;
          font-size: 0.8rem;
          gap: 8px;
          @media screen and (max-width: 600px) {
            display: none;
          }
        }
        a {
          display: flex;
          justify-content: center;
          padding: 0.3rem;
          text-decoration: none;
          color: var(--dark-grey-color);
          background-color: white;
          border-radius: 50%;
          transition: all ease-in-out var(--timer-short);
          @media screen and (max-width: 600px) {
            opacity: 0;
          }
          &:hover {
            background-color: rgb(223, 222, 222);
          }
        }
      }
    }
  }

  .navbar {
    background-color: white;
    .navbar__content {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 1.1rem 0px;
      @media screen and (max-width: 600px) {
        // HIDE and swap to
        display: none;
      }
      .home-link {
        display: flex;
        align-items: center;
        img {
          width: 120px;
        }
      }

      .nav__links {
        display: flex;
        gap: 10px;
        font-size: 1rem;

        .navigation {
          display: flex;
          align-items: center;
          .sub-nav {
            display: none;
            opacity: 0;
            transition: all ease-in var(--timer-short);
          }
        }

        .navigation:hover {
          position: relative;

          .sub-nav {
            display: flex;
            position: absolute;
            top: 36px;
            width: 450px;
            opacity: 1;
            background-color: white;
            box-shadow: var(--box-shadow-1);
            border-radius: var(--border-radius-1);
            animation: fade-in 0.6s forwards;

            div {
              display: flex;
              flex-direction: column;
              width: 100%;
              padding: 0.8rem;
              .sub-nav-link {
                font-size: 0.9rem;
                display: flex;
                align-items: center;
                gap: 8px;
                width: 100%;
                padding: 0.2rem;
                padding-left: 0.5rem;
                color: var(--grey-500);
                text-decoration: none;
                transition: all ease-in-out var(--timer-short);
                border-bottom: none;
                i {
                  color: var(--grey-400);
                }
                &:hover {
                  background-color: var(--grey-100);
                }
              }
            }
            .booking-btn {
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              gap: 5px;
              width: 100%;
              background-color: var(--secondary-color);
              border-top-right-radius: var(--border-radius-1);
              border-bottom-right-radius: var(--border-radius-1);
              text-decoration: none;
              color: white;
              transition: all ease-in-out var(--timer-short);
              p {
                font-size: 0.9rem;
              }
              span {
                margin-bottom: 0.5rem;
                padding: 0.2rem 1.5rem;
                border: 1px solid white;
                border-radius: var(--border-radius-1);
                font-weight: 500;
                transition: all ease-in-out var(--timer-short);
              }
              &:hover {
                background-color: var(--secondary-hover);
                span {
                  background-color: white;
                  color: var(--secondary-color);
                }
              }
            }
          }
        }
      }
    }
  }
}

.nav-link {
  display: flex;
  align-items: center;
  gap: 3px;
  font-size: 0.9rem;
  padding: 0.3rem 0.7rem 0.4rem 0.7rem;

  text-decoration: none;
  transition: all ease-in-out var(--timer-short);
  color: var(--grey-500);
  border-bottom: 2px solid white;
  i {
    font-size: 0.7rem;
    font-weight: bold;
    color: var(--grey-300);
  }
}
.nav-link.active {
  color: var(--secondary-color);
  border-bottom: 2px solid var(--secondary-color);
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
